.ScheduleInput-input-box {
    border: 2px solid grey;
    height: 200px;
    width: 100%;
    -webkit-transition: border 0.5s;
    -o-transition: border 0.5s;
    transition: border 0.5s;
}

.ScheduleInput-success-border {
    border: 3px solid lightgreen;
}

.ScheduleInput-failed-border {
    border: 3px solid red;
}

@-webkit-keyframes notice-animation {
    0% {-webkit-transform: scale(0.001, 0);transform: scale(0.001, 0); background-color: black;}
    50% {-webkit-transform: scale(0.001, 1);transform: scale(0.001, 1);}
}

@keyframes notice-animation {
    0% {-webkit-transform: scale(0.001, 0);transform: scale(0.001, 0); background-color: black;}
    50% {-webkit-transform: scale(0.001, 1);transform: scale(0.001, 1);}
}
.ScheduleInput-notice {
    display: inline-block;
    margin: 10px;
    -webkit-animation-name: notice-animation;
            animation-name: notice-animation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.ScheduleInput-parse-failed {
    text-align: initial;
}

@-webkit-keyframes blinking {
    50% {opacity: 0;}
}

@keyframes blinking {
    50% {opacity: 0;}
}
.ScheduleInput-arrow {
    font-size: 2em;
    -webkit-animation: blinking 1s linear 5;
            animation: blinking 1s linear 5;
}



